	module.exports = {"data":{"challengeNode":{"title":"Problem 98: Anagramic squares","description":["By replacing each of the letters in the word CARE with 1, 2, 9, and 6 respectively, we form a square number: 1296 = 362. What is remarkable is that, by using the same digital substitutions, the anagram, RACE, also forms a square number: 9216 = 962. We shall call CARE (and RACE) a square anagram word pair and specify further that leading zeroes are not permitted, neither may a different letter have the same digital value as another letter.","Using words.txt (right click and 'Save Link/Target As...'), a 16K text file containing nearly two-thousand common English words, find all the square anagram word pairs (a palindromic word is NOT considered to be an anagram of itself).","What is the largest square number formed by any member of such a pair?","NOTE: All anagrams formed must be contained in the given text file."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-98-anagramic-squares","blockName":"Project Euler","tests":[{"text":"<code>euler98()</code> should return 18769.","testString":"assert.strictEqual(euler98(), 18769, '<code>euler98()</code> should return 18769.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler98() {\n  // Good luck!\n  return true;\n}\n\neuler98();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-99-largest-exponential","id":"5900f3cf1000cf542c50fee1"},"slug":"/coding-interview-prep/project-euler/problem-98-anagramic-squares"}}

